import axios from "axios";

const BASEURL = "https://api.nytimes.com/svc/search/v2/articlesearch.json";
const APIKEY = "c42c8ce0786b4f0cb1fcc02fca9c7ea4";

export default {
  search: function(query,bd,ed) {
    return axios.get(
        {
            url: BASEURL,
            qs: {
                'api-key': APIKEY,
                'q': query,
                'begin_date': bd,
                'end_date': ed  
            }
        })
  }
};



